.c{
    height: 80vh;
    position: relative;
}

.c-bg{
    width: 20px;
    height: 100%;
    background-color: #2e86de;
    position: absolute;
}

.c-wrapper{
    display: flex;

    padding: 50px;
}

.c-left{
    flex: 1;
}

.c-title{
    font-size: 60px;
    width: 50%;
}

.c-info{

}

.c-infor-item{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 45%;
}

.c-icon{
    width: 30px;
    height: 30px;
    margin-right: 20px;
}

.c-right{
    flex: 1;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

}

.c-desc{
    font-weight: 200;
}

form{
    margin-top: 20px;
}

input{
    width: 50%;
    height: 50px;
    padding: 0px 12px;
    margin: 12px 0px;
    font-size: 14px;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

textarea{
    width: 90%;
    padding: 12px 12px;
    margin: 12px 0px;
    font-size: 14px;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);

}

button{
    border: none;
    padding: 16px 32px;
    background-color: #2e86de;
    color: #fff;
    font-weight: 500;
    cursor: pointer;
}

@media screen and (max-width: 480px){
    .c-wrapper{
        flex-direction: column;
        padding: 0px 30px;
    }
    .c-title{
    font-size: 30px;
    width: 100%;
    }
    .c-infor-item{
        width: 100%;
        margin: 20px 0px;
    }
    .c-bg{
        display: none;
    }

    input{
        width: 100%;
        padding: 0px 0px;
        margin: 4px 0px;

    }
    button{
        float: right
    }
    form{
        margin-bottom: 50px;
    }
    textarea{
        width: 100%;
        padding: 0;
    }
    .c-desc{
        display: none;
    }
}