.a{
    height: 100vh;
    display: flex;
    align-items: center;
}

.a-left{
    flex: 1;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

}

.a-right{
    flex: 1;

}

.a-title{
    font-weight: 400;

}

.a-sub{
    margin: 20px 0px;
}

.a-desc{
    font-weight: 300;
}

.a-card{
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}

.a-card.bg{
    position: absolute;
    top: 50px;
    left: 65px;
    background-color: #2e86de
}

.a-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.a-award{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
}

.a-award-img{
    width: 120px;
    height: 120px;
    border-radius: 20px;
}

.a-award-texts{
    width: 80%;
}

.a-award-title{
    font-weight: bold;
    margin-bottom: 10px;
        font-size: 24px;
}

@media screen and (max-width: 480px) {
    .a{
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
    }
    .a-left{
        width: 100%;
    }
    .a-card{
        height: 25vh;
    }
    .a-card.bg{
        display: none;
    }
    .a-right{
        padding: 10px;
    }

    .a-award{
        flex-direction: column;
    }

    .a-award-img{
        width: 80px;
        height: 80px;
        border-radius: 10px;
    }
    .a-award-title{
        margin-top: 10px;
    }
}