.p{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.p-browser{
    height: 20px;
    background-color:  rgb(180, 180, 180);
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
}

.p-circle{
    width: 6px;
    height: 6px;
    background-color: #fff;
    border-radius: 50%;
    margin: 3px;
}

.p-img{
    width: 100%;
    /* height: 90%; */
   object-fit: cover;
   transition: all 1.2s ease ;
   overflow: hidden;
}

.p:hover .p-img{
    transform: translateY(-35%);
}

@media screen and (max-width: 480px) {
    .p{
        width: 90%;
    }
}